'use client';

import { Battery } from '@/src/lib/batteries/batteryTypes';
import { exportBatteriesToExcel } from '@/src/lib/batteries/exportBatteries';
import { importBatteriesFromExcel } from '@/src/lib/batteries/importBatteries';
import { Button } from '@/src/components/ui/button';
import { Input } from '@/src/components/ui/input';

interface Props {
  batteries: Battery[];
  setBatteries: React.Dispatch<React.SetStateAction<Battery[]>>;
  searchTerm: string;
  setSearchTerm: React.Dispatch<React.SetStateAction<string>>;
}

export default function BatterySearchImportExport({
  batteries,
  setBatteries,
  searchTerm,
  setSearchTerm,
}: Props) {
  const handleImport = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    const imported = await importBatteriesFromExcel(file);
    setBatteries(imported);
  };

  const handleExport = () => {
    exportBatteriesToExcel(batteries);
  };

  return (
    <div className="flex flex-col md:flex-row items-start md:items-center justify-between mb-4 gap-4">
      <Input
        placeholder="Search battery..."
        value={searchTerm}
        onChange={(e) => setSearchTerm(e.target.value)}
        className="w-full md:w-[300px]"
      />

      <div className="flex gap-2">
        <Button variant="outline" onClick={handleExport}>
          Export to Excel
        </Button>

        <label className="cursor-pointer inline-flex items-center px-4 py-2 border rounded bg-white text-sm">
          Import Excel
          <input
            type="file"
            accept=".xlsx,.xls"
            onChange={handleImport}
            className="hidden"
          />
        </label>
      </div>
    </div>
  );
}
